@import url('https://fonts.googleapis.com/css2?family=ABeeZee&family=Bree+Serif&family=Inter:wght@500;600&display=swap');

* {
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
}

body {
	font-family: 'Inter', sans-serif;
}

.app {
	background-image: url('./assets/cold-bg.jpg');
	background-size: cover;
	background-position: bottom;
	transition: 1s ease-out;
}
.app.warm {
	background-image: url('./assets//warm-bg.jpg');
}
main {
	min-height: 100vh;
	background-image: linear-gradient(
		to bottom,
		rgba(0, 0, 0, 0.2),
		rgba(0, 0, 0, 0.8)
	);
	padding: 25px;
}

.search-box {
	width: 100%;
	margin: 0 0 75px;
}

.search-box .search-bar {
	display: block;
	width: 100%;
	padding: 15px;
	appearance: none;
	background: none;
	border: none;
	outline: none;
	background-color: rgba(255, 255, 255, 0.5);
	border-radius: 0px 0px 16px 16px;
	margin-top: -25px;
	box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
	color: #313131;
	font-size: 20px;
	transition: 0.4s ease;
}

.search-box .search-bar:focus {
	background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
	color: #fff;
	font-size: 32px;
	font-weight: 500;
	text-align: center;
	text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
	color: #fff;
	font-size: 20px;
	font-weight: 300;
	text-align: center;
	font-style: italic;
	text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
	text-align: center;
}
#error {
	text-align: center;
	font-size: 32px;
	font-weight: 500;
	color: #fff;
	text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
.header-box {
	width: 100%;
	margin: 30px auto;
	background-color: rgba(255, 255, 255, 0.2);
	border-radius: 16px;
	padding: 15px 25px;
	color: #fff;
	font-size: 90px;
	font-weight: bold;
	text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
	box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}
#heading {
	text-align: center;
	font-size: 32px;
	color: #fff;
	text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
.weather-box .temp {
	position: relative;
	display: inline-block;
	margin: 30px auto;
	background-color: rgba(255, 255, 255, 0.2);
	border-radius: 16px;
	padding: 15px 25px;
	color: #fff;
	font-size: 90px;
	font-weight: 900;
	text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
	text-align: center;
	box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
	color: #fff;
	font-size: 48px;
	font-weight: 700;
	text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

@media screen and (min-width: 500px) {
	.app {
		background-image: url('./assets/full-size-cold.jpg');
	}
	.app.warm {
		background-image: url('./assets//full-size-warm.jpg');
	}
}

@media screen and (min-width: 768px) {
	.search-box {
		width: 50%;
		margin: 0 auto 75px;
	}
	.header-box {
		width: 70%;
		margin: 30px auto;
	}
}
